import Vue from 'vue'
import Router from 'vue-router'
import Topiclist from '../components/Topiclist'
import Topic from '../components/Topic'
import Login from '../components/Login'
import User from '../components/User'
import UserChild from '../components/userChild'
import Center from "../components/Center"
import Logout from "../components/Logout"
import Create from "../components/Create"
import Edit from "../components/Edit"
import Msg from "../components/Msg"

Vue.use(Router)

// 
const originalPush = Router.prototype.push
   Router.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/topiclist/all'
    },
    {
      path:'/topiclist/:name',
      component: Topiclist,
      props:true,//props
    },
    {
      path:'/topic/:id',
      component:Topic,
      props:true
    },
    {path:'/login',component:Login},
    {path:'/user/:username',component:User,props:true},
    {path:'/userChild/:username',component:UserChild,props:true},
    {path:'/center',component:Center},//
    {path:'/logout',component:Logout},//
    {path:'/create',component:Create},
    {path:'/topic/:id/edit',component:Edit,props:true},//
    {path:'/msg',component:Msg}//
  ]
})



