
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
props: ["username"],
components: {
Card,
Cell,
Group
},
data: function() {
return {
userInfo: null,
collect_topics: []
};
},
beforeRouteEnter(to, from, next) {
next(function(vm) {

vm.$http.get("/user/" + vm.username).then(function(res) {
console.log(res.data.data);
vm.userInfo = res.data.data;
});

vm.$http.get("/topic_collect/" + vm.username).then(function(res) {
vm.collect_topics = res.data.data;
});
});
},
beforeRouteUpdata(to, from, next) {
var app = this;
this.$http.get("/user/" + to.params.username).then(function(res) {

app.userInfo = res.data.data;
});

this.$http.get("/topic_collect/" + this.username).then(function(res) {
this.collect_topics = res.data.data;
});
next();
}
};



