import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
const store = new Vuex.Store({
    state:{
        topics:[//tab 
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''},
        ],
        isShowMoreMenus:false, //actionsheet,
        // moreMenus:[],     //
        isLogined:false,  //
        unLoginedMenus:{
            login:''
        },
        loginedMenus:{
            center:'',
            msg:'',
            create:'',
            logout:''
            
        },
        loginname:'',//
        accesstoken:'',//
        author_id:''
    },
    getters:{
        moreMenus(state){  //statestorestate
            if(state.isLogined){

                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations:{
        // actionsheet
        mutationsIsShowMoreMenus(state,payload){
            state.isShowMoreMenus=payload;
        },
        //
        mutationsIsLogined(state){
            state.isLogined=true
        },
        // 
        mutationsLoginname:function(state,payload){
            state.loginname=payload;
        },
        // 
        mutationsAccesstoken:function(state,payload){
            state.accesstoken=payload;
        },
        // id
        mutationsAuthorId:function(state,payload){
            state.authorId=payload;
        },
        // 
        mutationsLogout:function(state){
            state.isLogined=false;
            state.loginname='';
            state.accesstoken='';
            state.authorId=''

        }
    },
    actions:{}
})

export default store;


