
import Msg from 'vux/src/components/msg/index.vue'

export default {
components:{
Msg
},
data:function(){
return{
buttons: [{
type: 'primary',
text: '',
link:'/login'
}],
}
},
created:function(){




this.$store.commit('mutationsLogout')

window.localStorage.clear();

this.$router.push('/')
}
}



