
import {mapGetters,mapMutations} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: 'app',
data:function(){
return{
badgeText:''
}
},
components: {
XHeader,
Actionsheet,
Badge
},

mounted(){//
var loginStatus=window.localStorage.getItem('loginStatus')
if(loginStatus){   //
this.mutationsIsLogined()
this.mutationsLoginname(localStorage.getItem('loginname'))
this.mutationsAccesstoken(localStorage.getItem('accesstoken'))
this.mutationsAuthorId(localStorage.getItem('author_id'))
}
if(this.$store.state.isLogined){//
this.getUnreadMsgCount();

var app=this;
window.setInterval(function(){//
app.getUnreadMsgCount();
},1000*5)
}
},
computed:{

...mapGetters(['moreMenus']),
isShowMoreMenus:{ //getset
get(){//get
return this.$store.state.isShowMoreMenus
},
set(nval){//set
this.$store.commit('mutationsIsShowMoreMenus',nval)
}
}
},
methods:{


...mapMutations(['mutationsIsLogined','mutationsLoginname','mutationsAccesstoken','mutationsAuthorId']),

routeToHome(){

this.$router.push('/');
},

showMoreMuens(){
this.isShowMoreMenus=true
},

toMenu(menuKey, menuItem){



this.$router.push('/'+menuKey)
},

getUnreadMsgCount:function(){

var app=this;
this.$http.get('message/count',{
params:{
accesstoken :this.$store.state.accesstoken
}
}).then(function(res){

app.badgeText=res.data.data;
})
}
}
}



